$font-stack: Helvetica, sans-serif;
$orange: #ff8400;
$black: #1f2022;


body,p {
  font-family: $font-stack;
  color: $orange;
  font-size: 1em;
  font-weight: 400;
  line-height: 1.5; }

.image {
  max-width: 40%;

  @media screen and (max-width: 767px) {
    max-width: 80%; } }


h1,h2,h3,h4,h5,h6 {
  font-family: $font-stack;
  font-weight: 700; }

@for $i from 1 through 6 {
  h#{7 -$i} {
    font-size: $i * 0.5em; } }



.box1 {
  height: 100%;
  background-image: url('https://wallpaperaccess.com/full/2735406.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }

.box2 {
  height: 100%;
  background-color: black;
  background-image: url('https://miro.medium.com/max/8032/1*pc5ewtE58QjePhYMUxIRAg.jpeg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }


.box3 {
  height: 100%;
  background-color: black; }

.box4 {
  height: 3vh;
  background-color: $orange; }

.footer {
  height: 100%;
  background-color: $black; }

.footer-bottom {
  height: 5vh;
  background-color: $orange; }

canvas {
  display: block;
  width: 100vw;
  height: 100vh; }

.btn-orange {
  color: white;
  background-color: $orange;
  border-color: $orange; }

.gallery {
  width: 100%;
  max-height: 300px;

  &:hover {
    opacity: 0.5; } }

